</template>

<script>
  import deviceShell from './DeviceShell'
  import {formatDate} from './../format/data';
	export default {
    components: {
      deviceShell
    },
		data: function() {
			return {
        activeShell:false,
        deviceActive:false,
        activeDevice:false,
        LogStatus:false,
			  isActive:true,
        DeviceKey:'',
        shellUrl:'',
        shellLink:true,
			    item:{

          },
				ruleForm2: {
          SN:'',
					Name: '',
					Mac: '',
          Description: ''
				},
        ruleForm3: {
          SN:'',
          Name: '',
          Mac: '',
          Description: ''
        },
				rules: {
          SN: [{
            required: true,
            message: '',
            trigger: 'blur'
          }],
          ProductKey: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
				},
				updateDevice: false,
                listLoading: false,
			}
		},
        mounted: function() {
            this.$nextTick(function() {
                this.detailView();
            })
        },
        filters:{
            formatLinkType(val,column){
                return val == "1" ? 'Wi-Fi' : val == "2" ? 'Zigbee' : val == "3" ? '' : val == "4" ? 'Mesh' : val == "5" ? 'PC' : '';
            },
            formatLinkType(val,column){
                return val == "1" ? 'Wi-Fi' : val == "2" ? 'Zigbee' : val == "3" ? '' : val == "4" ? 'Mesh' : val == "5" ? 'PC' : '';
            },
            formatIsActive(val,column){
                return val == 0 ? '' : val == 1 ? ''  : '';
            },
            formatIsOnline(val,column){
                return val == 0 ? '' : val == 1 ? ''  : '';
            },
            formatTime(val){
              if(val==undefined||val==""){
                return ""
              }else {
                return  val+'  ';
              }

            },
          formatDate(val) {
            if(val==undefined||val==""){
              return ""
            }else {
              let date = new Date(val*1000);
              return formatDate(date, "yyyy-MM-dd hh:mm");
            }

          },
        },
		methods: {
            detailView: function() {
                let self = this,
                    productId = sessionStorage.getItem('productId'),
                    deviceId = sessionStorage.getItem('deviceId'),
                    _url = "/products/" + productId+"/devices/" + deviceId,
                    _method = "GET",
                    sign1 = sessionStorage.getItem('login_TokenId') +_url + _method ,
                    sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
                //
                // 
                this.$http({
                    url: "/iotadmin" + _url,
                    method: _method,
                    headers: {
                        'content-type': 'application/json',
                        'Rtt-Sign': sign
                    },
                }).then((res) => {
                    console.log(res.data)
                    if(res.data == null) {
                        self.$message.error('');
                    } else {
                        this.listLoading=true,
                        this.item = res.data;
                        if(res.data.LogStatus == 0){
                          this.LogStatus=true;
                        }else{
                          this.LogStatus= false;
                        }
                        
                      const json1=res.data;
                        const string1= json1.toString();
                    }
                }).catch(function(error) {
                    if(error.response) {
                        console.log(error.response.data);
                        console.log("222:" + error.response.status);
                        console.log("333:" + error.response.header);
                        //2xx
                        if(error.response.data.ErrCode = "40000004") {
                            self.$message.error('');
                        } else {
                            self.$message.error('');
                            return false
                        }
                    } else {
                        console.log(error);
                        //
                        console.log('Error', error.message);
                        return false
                    };
                });
            },

			handleBack(back) {
				this.$router.go(-1);
			},
			handleNew() {
                this.listLoading=false,
                this.detailView();
			},

            handleDelete(val) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let self = this,
                        productId = sessionStorage.getItem('productId'),
                        _url = "/products/" + productId+"/devices/" + val,
                        _method = "DELETE",
                        sign1 = sessionStorage.getItem('login_TokenId') + _url + _method,
                        sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
                    console.log(sign1)
                    console.log(sign)
                    this.$http({
                        url: "/iotadmin" + _url,
                        method: _method,
                        headers: {
                            'content-type': 'application/json',
                            'Rtt-Sign': sign
                        },
                    }).then((res) => {
                        console.log(res.data)
                        if(res.data == "") {
                            this.$message({
                                    type: 'success',
                                    message: '!',
                                },
                                this.$router.push('/deviceList'));
                          let newId = sessionStorage.getItem('productId'),
                            newName = sessionStorage.getItem('productName');
                          this.$router.push({
                            name: 'deviceList',
                            params: {
                              productId: newId,
                              productName: newName
                            }
                          })
                        } else {
                            this.$message({
                                type: 'info',
                                message: ''
                            });
                        }
                        this.information = res.data;
                    }).catch(function(error) {
                        if(error.response) {
                            console.log(error.response.data);
                            console.log("222:" + error.response.status);
                            console.log("333:" + error.response.header);
                            //2xx
                            if(error.response.data.ErrCode = "40000004") {
                                self.$message.error('');
                            } else {
                                self.$message.error('');
                                return false
                            }
                        } else {
                            console.log(error);
                            //
                            console.log('Error', error.message);
                            return false
                        };
                    });

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },

            handleEdit(val){
                console.log(val)
                let self =this;
                self.ruleForm2={
                  SN: val.SN,
                    Name: val.Name,
                    Mac: val.Mac,
                  Description: val.Description,
                };
              sessionStorage.setItem('DeviceId',val.DeviceId)
                self.updateDevice=true;

            },
      handleActive(val) {
        let self =this;
        self.ruleForm3 = {
          SN: self.item.SN,
          Name: self.item.Name,
          Mac: self.item.Mac,
          DeviceId: self.item.DeviceId
        };
        self.activeDevice = true;
      },

      submitForm3(formName){
        let self = this,
          productId = sessionStorage.getItem('productId'),
          _url = "/devices/active",
          _method = "POST",
          _body = {            //
            'SN': self.ruleForm3.SN,
            'ProductId': productId,
            'Sign': this.md5(self.ruleForm3.ProductKey+self.ruleForm3.SN),
          },
          body = JSON.stringify(_body);
        //HTTP   
        this.$http({
          url: "/iotadmin" + _url, //
          method: _method, //
          data: body, //
        }, body).then((res) => { //
          console.log(res)
          this.$notify({
            title: 'GOOD',
            message: '',
            type: 'success'
          });
          console.log(res.data.DeviceKey)
          self.DeviceKey=res.data.DeviceKey
          this.$refs['ruleForm'].resetFields();
          self.activeDevice = false;
          self.deviceActive = true;
        }).catch((error) => { //
          console.log("error:" + error)
          if (error.response) {
            console.log(error.response.data);
            console.log(error.response.data.ErrCode);
            //2xx
            if (error.response.data.ErrCode == 40000101) {
              self.$message.error('');
            } else {
              self.$message.error('');
              return false;
            }
          } else {
            //
            console.log('Error', error.message);
            self.$message.error('');
            return false;
          }
        });
      },

            submitForm2(formName) {
                let self = this,
                    productId = sessionStorage.getItem('productId'),
                    DeviceId = sessionStorage.getItem('DeviceId'),
                    _url = "/products/"+productId+"/devices/"+DeviceId,
                    _method = "PUT",
                    _body = {            //
                        'Name': self.ruleForm2.Name,
                        'Mac': self.ruleForm2.Mac,
                    },
                    body = JSON.stringify(_body),
                    sign1 = sessionStorage.getItem('login_TokenId') +_url + _method+body,
                    sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
                //
                // 

                // 
                self.$refs[formName].validate((valid) => {
                    if(valid) {
                        console.log(0);
                        //HTTP   
                        this.$http({
                            url: "/iotadmin"+_url, //
                            method: _method, //
                            headers: { // 
                                'content-type': 'application/json',
                                'Rtt-Sign': sign //
                            },
                            data: body, //
                        }).then((res) => { //
                            console.log("res:" + res)
                            this.add_suc2(res)

                        }).catch((error) => { //
                            console.log("error:" + error)
                            this.add_err2(error)
                        });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
                /*this.$router.push('/register2');*/
            },

            //
            add_suc2(res) {
                console.log(55555)
                /*(200,),*/
                if(res.status == 200) {
                    this.$notify({
                        title: 'GOOD',
                        message: '',
                        type: 'success'
                    });
                    let newId = sessionStorage.getItem('productId');
                    console.log('newId:' + newId);
                    this.$refs['ruleForm2'].resetFields();
                    this.updateDevice=false,
                    this.listLoading=false,
                        this.detailView();
                } else {
                    this.$message.error('');
                }

            },
            //
            add_err2(error) {
                var self = this;
                if(error.response) {
                    console.log(error.response.data);
                    console.log(error.response.data.ErrCode);
                    //2xx
                    if(error.response.data.ErrCode == 40000101) {
                        self.$message.error('');
                    } else {
                        self.$message.error('');
                        return false;
                    }
                } else {
                    //
                    console.log('Error', error.message);
                    self.$message.error('');
                    return false;
                }

            },
            handleLog(val){
             sessionStorage.setItem("DeviceId",val);
             this.$router.push("/deviceLog")
        },
      handleOpenLog(val){
        let self = this,
          productId = sessionStorage.getItem('productId'),
          _url = "/logs/" + val+"/open",
          _method = "POST",
          sign1 = sessionStorage.getItem('login_TokenId') + _url + _method,
          sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
        this.$http({
          url: "/iotadmin" + _url,
          method: _method,
          headers: {
            'content-type': 'application/json',
            'Rtt-Sign': sign
          },
        }).then((res) => {
          console.log(res.data)
          if(res.data == "") {
            this.$message({
                type: 'success',
                message: '!',
              })
              this.LogStatus = false;
          } else {
            this.$message({
              type: 'info',
              message: ''
            });
          }
        }).catch(function(error) {
          if(error.response) {
            console.log(error.response.data);
            console.log("222:" + error.response.data);
            console.log("333:" + error.response.data.ErrCode);
            //2xx
            if(error.response.data.ErrCode = "40000202") {
              self.$message.error('');
            } else {
              self.$message.error('');
              return false
            }
          } else {
            console.log(error);
            //
            console.log('Error', error.message);
            return false
          };
        });
      },
      handleCloseLog(val){
        let self = this,
          productId = sessionStorage.getItem('productId'),
          _url = "/logs/" + val+"/close",
          _method = "POST",
          sign1 = sessionStorage.getItem('login_TokenId') + _url + _method,
          sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
        console.log(sign1)
        console.log(sign)
        this.$http({
          url: "/iotadmin" + _url,
          method: _method,
          headers: {
            'content-type': 'application/json',
            'Rtt-Sign': sign
          },
        }).then((res) => {
          console.log(res.data)
          if(res.data == "") {
            this.$message({
              type: 'success',
              message: '!',
            })
            this.LogStatus = true;
          } else {
            this.$message({
              type: 'info',
              message: ''
            });
          }
        }).catch(function(error) {
          if(error.response) {
            console.log(error.response.data);
            console.log("222:" + error.response.data);
            console.log("333:" + error.response.data.ErrCode);
            //2xx
            if(error.response.data.ErrCode = "40000202") {
              self.$message.error('');
            } else {
              self.$message.error('');
              return false
            }
          } else {
            console.log(error);
            //
            console.log('Error', error.message);
            return false
          };
        });
      },
        handleShell(val){
//          this.activeShell=true
          console.log(val)
          let self = this,
            _url = "/shell/" + val+"/open",
            _method = "POST",
            sign1 = sessionStorage.getItem('login_TokenId') + _url + _method,
            sign = this.md5(sign1) + "," + sessionStorage.getItem('login_Signature');
          console.log(sign1)
          console.log(sign)
          this.$http({
            url: "/iotadmin" + _url,
            method: _method,
            headers: {
              'content-type': 'application/json',
              'Rtt-Sign': sign
            },
          }).then((res) => {
            if(res.data != "") {
              console.log(res.data.URL)
              sessionStorage.setItem('shellUrl',res.data.URL)
              this.shellUrl=res.data.URL;
              this.activeShell=true
            } else {
              this.$message({
                type: 'info',
                message: ''
              });
            }
          }).catch(function(error) {
            if(error.response) {
              console.log(error.response.data);
              console.log("222:" + error.response.data);
              console.log("333:" + error.response.data.ErrCode);
              //2xx
              if(error.response.data.ErrCode = "40000202") {
                self.$message.error('');
              } else {
                self.$message.error('');
                return false
              }
            } else {
              console.log(error);
              //
              console.log('Error', error.message);
              return false
            };
          });
        },


        }
    }

</script>

<style>
	.ms-doc {
		width: 100%;
		max-width: 980px;
		font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
	}

	.ms-doc h3 {
		padding: 9px 10px 10px;
		margin: 0;
		font-size: 14px;
		line-height: 25px;
		background-color: #f5f5f5;
		border: 1px solid #d8d8d8;
		border-bottom: 0;
		border-radius: 3px 3px 0 0;
	}

	.ms-doc article {
		padding: 45px;
		word-wrap: break-word;
		background-color: #fff;
		border: 1px solid #ddd;
		border-bottom-right-radius: 3px;
		border-bottom-left-radius: 3px;
	}

	.ms-doc article>div {
		padding: 10px 0;
	}

	.span_l {
		text-align: right;
		color: #999;
	}

	.publish {
		margin-top: 20px;
	}

	.delete {
		float: right;
		color: #ff4949;
		font-size: 24px;
		margin-right: 10px;
		cursor: pointer;
		display: block;
		width: 30px;
	}

	.edit {
		float: right;
		font-size: 24px;
		color: #00d1b2;
		margin-right: 25px;
		cursor: pointer;
